.social-block {
  position: relative;

  &:hover {
    .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }
  }
}

.social-block__toggler {
  @include btn-reset;
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: $c-dark;
  border-radius: 50%;

  @media (max-width: $tablet) and (orientation: portrait) {
    display: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    display: none;
  }

  &::before {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: $c-light;
    border: 2px solid $c-light;
    z-index: -1;
    transition: all $duration-extra-long cubic-bezier(.6,-0.25,.47,1.71) 0.4s;
  }

  svg {
    @include center;
    display: block;
    width: 2.3rem;
    height: 2.2rem;
  }

  &:hover,
  .social-block--active & {
    color: $c-purple;

    &::before {
      width: 112.5%;
      height: 112.5%;
    }
  }

  &:focus {
    color: $c-purple;
    outline: none;

    &::before {
      border-color: $c-purple;
    }

    & + .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }
  }

  &:active,
  &:active:focus {
    color: $c-light;

    &::before {
      width: 100%;
      height: 100%;
      background-color: $c-purple;
      border-color: $c-purple;
    }
  }
}

.page-loaded .social-block__toggler {
  &:before {
    transform: translate(-50%, -50%) scale(1);
  }
}

.social-block__list {
  @include list-reset;
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: 1.5rem;
  opacity: 0;
  pointer-events: none;
  transition: all 500ms;

  @media (min-width: $stop-scaling) {
    margin-top: 15px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    height: 1.5rem;

    @media (min-width: $stop-scaling) {
      height: 15px;
    }
  }

  &:hover {
    opacity: 1;
    pointer-events: auto;
  }

  &:focus-within {
    opacity: 1;
    pointer-events: auto;
  }

  .social-block--active & {
    opacity: 1;
    pointer-events: auto;

    li {
      opacity: 1;

      &:nth-child(1) {
        transition-delay: 100ms;
      }

      &:nth-child(2) {
        transition-delay: 200ms;
      }

      &:nth-child(3) {
        transition-delay: 300ms;
      }
    }
  }

  li {
    flex: none;
    margin-bottom: 0.5rem;
    opacity: 0;
    transition: all 500ms;

    @media (min-width: $stop-scaling) {
      margin-bottom: 5px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin: 0 1.25rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin: 0 1.25rem;
    }
  }
}

.social-block__link {
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: $c-light;
  border: 2px solid transparent;
  border-radius: 50%;
  transition: all 500ms ease-in-out;

  @media (min-width: $stop-scaling) {
    width: 40px;
    height: 40px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    color: $c-light;
    background-color: $c-dark;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    color: $c-light;
    background-color: $c-dark;
  }

  &:hover {
    color: $c-purple;
  }

  &:focus {
    color: $c-purple;
    background-color: $c-light;
    border-color: $c-purple;
    outline: none;
  }

  &:active,
  &:active:focus {
    color: $c-purple;
    background-color: $c-light;
    border-color: $c-light;
  }

  svg {
    @include center;
  }

  &--fb {
    svg {
      width: 0.8rem;
      height: 1.7rem;

      @media (min-width: $stop-scaling) {
        width: 8px;
        height: 17px;
      }
    }
  }

  &--insta {
    svg {
      width: 1.5rem;
      height: 1.5rem;

      @media (min-width: $stop-scaling) {
        width: 15px;
        height: 15px;
      }
    }
  }

  &--vk {
    svg {
      width: 1.7rem;
      height: 1rem;

      @media (min-width: $stop-scaling) {
        width: 10px;
        height: 10px;
      }
    }
  }
}

@media (min-width: ($mobile + 1)) and (orientation: landscape) {
  [data-screen="story"][data-slide="1"] {
    .social-block__toggler {
      color: $c-purple;

      &:hover,
      .social-block--active & {
        color: $c-dark;
      }

      &:focus {
        color: $c-dark;

        &::before {
          border-color: $c-dark;
        }

      }

      &:active,
      &:active:focus {
        color: $c-light;

        &::before {
          background-color: $c-dark;
          border-color: $c-dark;
        }
      }
    }

    .social-block__link {
      &:hover {
        color: $c-dark;
      }

      &:focus {
        color: $c-dark;
        border-color: $c-dark;
      }

      &:active,
      &:active:focus {
        color: $c-dark;
      }
    }
  }

  [data-screen="story"][data-slide="2"] {
    .social-block__toggler {
      color: #3e72ee;

      &:hover,
      .social-block--active & {
        color: #90b8e2;
      }

      &:focus {
        color: #90b8e2;

        &::before {
          border-color: #90b8e2;
        }

      }

      &:active,
      &:active:focus {
        color: $c-light;

        &::before {
          background-color: #90b8e2;
          border-color: #90b8e2;
        }
      }
    }

    .social-block__link {
      &:hover {
        color:  #90b8e2;
      }

      &:focus {
        color:  #90b8e2;
        border-color:  #90b8e2;
      }

      &:active,
      &:active:focus {
        color:  #90b8e2;
      }
    }
  }

  [data-screen="story"][data-slide="3"] {
    .social-block__toggler {
      color: #90b8e2;

      &:hover,
      .social-block--active & {
        color: #3e72ee;
      }

      &:focus {
        color: #3e72ee;

        &::before {
          border-color: #3e72ee;
        }

      }

      &:active,
      &:active:focus {
        color: $c-light;

        &::before {
          background-color: #3e72ee;
          border-color: #3e72ee;
        }
      }
    }
    .social-block__link {
      &:hover {
        color:  #3e72ee;
      }

      &:focus {
        color:  #3e72ee;
        border-color:  #3e72ee;
      }

      &:active,
      &:active:focus {
        color:  #3e72ee;
      }
    }
  }

  [data-screen="story"][data-slide="4"] .social-block__toggler {
    color: $c-dark;

    &:hover,
    .social-block--active & {
      color: $c-purple;
    }

    &:focus {
      color: $c-purple;

      &::before {
        border-color: $c-purple;
      }

    }

    &:active,
    &:active:focus {
      color: $c-light;

      &::before {
        background-color: $c-purple;
        border-color: $c-purple;
      }
    }
  }
}
